.corner {
    position: fixed;
    bottom: 50px; 
    right: 50px; 
    z-index: 9999; 
    
  }
  
  .corner img {
    cursor: pointer;
  }

 

  .botIcon {
    /* background-color: rgb(194, 240, 225); */
    transition: transform 0.3s;
    /* border-radius: 50%;
    padding: 12px; */
  }
  
  .corner:hover .botIcon {
    transform: scale(1.1); /* Change the scale value as needed for zoom level */
  }
 