.chat-container {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 50px auto;
    border: 1px solid #ccc;
    border-radius: 15px;
    overflow: hidden;
  }
  
  .messages {
    padding: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .message {
    padding: 5px 12px;
    margin-bottom: 5px;
    max-width: 80%;
    
    font-size: 14px;
  }
  
  .user {
    align-self: flex-end;
    background-color: #c2f8c4;
    border-radius:15px 1px 15px 15px;
  }
  
  .bot {
    border-radius:15px 15px 15px 1px;
    align-self: flex-start;
    background-color: #f8ff95;
  }
  
  .input-container {
    padding: 10px;
    background-color: #fccccc;
  }
  
  input[type='text'] {
    width: calc(100% - 20px);
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
  }
  
  .chat-container {
    position: fixed;
  bottom: 80px;
  right: 70px;
  width: 300px;
  height: 550px;
  border: 1px solid #ccc;
    overflow-y: scroll;
  }
  .messages-container {
    height: calc(100% - 40px);
    overflow-y: auto;
  }
  .input-container input {
    width: calc(100% - 20px);
    padding: 8px;
    border: 1px solid #ff9a9a;
    border-radius: 5px;
    outline: none;
  }

  /* Hide the default scrollbar */
.chat-container::-webkit-scrollbar {
    width: 0px;
  }
.messages-container::-webkit-scrollbar {
    display: none;
}
